*{
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.grid-area{
    margin-top: 30px;
    table{
        max-width: 900px;
        margin: auto;
        user-select: none;
    }
    td{
        align-content: center;
        text-align: center;
        padding: 10px;
        
        &.selected{
            color: rgba(255, 103, 15, 0.973);
            font-family: Arial;
        }
    } 
}




